import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import { Box, Dialog, Link, Slider, Stack, Typography } from "@mui/material";
import HHButton from "../HH-Button";
import { Button } from "@mui/material";
import CloseIcon from '@mui/icons-material/Close';
import { Divider } from "@mui/material";
import { LinearProgress } from "@mui/material";
import Pdf from './gruble.pdf';

function Gruble() {
  const [time, setTime] = useState(75);
  const [timeLeft, setTimeLeft] = useState(75);
  const [timerActive, setTimerActive] = useState(false);
  const [usedTopics, setUsedTopics] = useState([]);
  const [currentTopics, setCurrentTopics] = useState([]);
  const [letters, setLetters] = useState('');
  const [modalOpen, setModalOpen] = useState(false);
 
  const bokstaver = ['A', 'B', 'D', 'E', 'F', 'G', 'H', 'I', 'J', 'K', 'L', 'M', 'N', 'O', 'P', 'R', 'S', 'T', 'U', 'V', '?'];

  const temaer = ['Idretter', 'Idrettsutvere', 'Kjente nordmenn', 'Drikker', 'Desserter', 'Byer', 'Hovedsteder', 'Sprk', 'Spill', 'Bakverk', 'yer', 'Filmer', 'Artister', 'Godteri', 'Butikker', 'Dyr', 'TV-serier', 'Lter', 'Klesplagg', 'Kjrety', 'Fotballag', 'Land', 'Kjendiser', 'Skuespillere', 'Middagsretter', 'Sykdommer', 'Grunnstoffer', 'Krydder', 'Hunderaser', 'Mbler', 'Aviser/blader', 'Kommuner', 'Adjektiver', 'Telefonapp', 'Instrument', 'Yrker', 'Plegg', 'Jentenavn', 'Guttenavn', 'Forfattere', 'Bker', 'Bedrifter', 'Klesmerker', 'Fjell', 'Bilmerker', 'Grnnsaker', 'Kjente bygninger', 'Frukt', 'Planter', 'Verb'];

  const handleChange = (e, val) => {
    setTime(val);
    setTimeLeft(val);
  }
  const startTime = () => {
    setTimeLeft(time);
    setTimerActive(true);
  }
  const closeModal = () => {
    setModalOpen(false);
    setTimerActive(false);
    setTimeLeft(time);
  }
  const showTopic = () => {
      if (usedTopics.length === temaer.length) {
        setUsedTopics([]);
        alert('Du har n vrt igjennom alle temaene. Temaene vil fra neste runde bli brukt p nytt i tilfeldig rekkeflge!')
        return
      }
      const newTopics = [];
      for (let i = 0; i < 5; i++) {
        const n = Math.floor(Math.random() * temaer.length);
        if (newTopics.includes(temaer[n]) || usedTopics.includes(temaer[n])) {
          i--;
          continue
        }
        newTopics[i] = temaer[n]
      }
      setUsedTopics(usedTopics.concat(newTopics))
      setCurrentTopics(newTopics);
    }
  const showLetters = () => handleLetters(5)
  const showLetter = () => handleLetters(1)

  const handleLetters = (a) => {
    setTimeLeft(time)
    setModalOpen(true)
    if (a < 2) {
      setLetters(bokstaver[Math.floor(Math.random() * bokstaver.length)])
    } else {
      const newLetters = [];
      for (let i = 0; i < 5; i++) {
        const n = Math.floor(Math.random() * bokstaver.length);
        if (newLetters.includes(bokstaver[n])) {
          i--;
          continue
        }
        newLetters[i] = bokstaver[n]
      }
      setLetters(newLetters);
    }
  }
  useEffect(() => {
    if (!timeLeft) {
      const sound = new Audio(require('./airhorn.mp3'));
      sound.play();
      setTimerActive(false);
      return;
    }
    if (timerActive) {
      const i = setInterval(()=> {
      setTimeLeft(() => timeLeft - 1);
    }, 1000);
    return () => clearInterval(i);  
  } 
  }, [timeLeft, timerActive]);

  return (
    <Stack textAlign="center" spacing={2}>
      <Typography variant="h3" color="primary">Gruble</Typography>
      <Link variant="h7" href={Pdf} target="_blank" rel="noreferrer">Last ned grubleark her!</Link>
      <Typography variant="h6" color="primary">Velg hvor lang tid hver runde skal vare</Typography>
      <Slider onChange={handleChange} valueLabelDisplay="auto" defaultValue={75} step={15} marks min={45} max={450} />
      <Typography variant="h7" color="primary">Du har valgt {time} sekunder</Typography>
      <HHButton click={showTopic} text="Vis nye temaer" />
    { currentTopics.length > 0 ? (
      <>
        <Box >
          {currentTopics.map((val, i) => (
                <Typography variant="h6" key={i} color="primary"> {currentTopics[i]} </Typography>
            ))}
        </Box>
        <HHButton click={showLetter} text="F en bokstav"/>
        <HHButton click={showLetters} text="F fem bokstaver" />
      </>
    ):''}

        <Dialog
        fullWidth
        open={modalOpen}
        onClose={closeModal}
        sx={{textAlign: "center", py: "20px"}}
        >
          <CloseIcon onClick={closeModal} sx={{ position: "absolute", top: 5, right: 5, cursor: "pointer"}}/>
          <Stack width={9/10} spacing={1.5} sx={{position: "relative", left: "5%", my: 1}}>
          <Typography variant="h3" color="primary">{letters}</Typography>
          <Divider color="primary">Tid</Divider>
          <LinearProgress variant="determinate" value={(time - timeLeft)/time * 100} min={0} max={100}
          sx={{
            height: 10, borderRadius: 5
          }}/>
          <Typography variant="h7" color="primary">Det er {timeLeft} sekunder igjen</Typography>
          {!timerActive ? (
            <Stack>
            <Button onClick={startTime} variant="outlined" sx={{mb: 2}}>Start tiden</Button>
              <Divider variant="middle">Nye bokstaver</Divider>
              <Button onClick={showLetter}>F en ny bokstav</Button>
              <Divider variant="middle" sx={{width: 1/2, margin: 'auto'}}/>  
              <Button onClick={showLetters}>F fem ny bokstaver</Button>
            </Stack>
            ):("")}
          </Stack>
        </Dialog>
    </Stack>
  );
}


export default Gruble;